import React, { Component } from "react";
import styled from "styled-components";
import { data } from "../../pages/data.json";

const Div = styled.div`
  background: #f2f7fa;
`;

const Content = styled.div`
  max-width: 980px;
  padding: 35px 15px;
  margin: 0 auto;
  .more-title {
    border-bottom: 1px solid #e9e8e7;
    padding-bottom: 10px;
  }
`;

const More = styled.div``;

class MoreRelation extends Component {
  state = {
    relationArticle: 0
  };
  componentDidMount = () => {
    const length = data.length;
    const chooseArticle = this.getRandom(length);
    this.setState({
      relationArticle: chooseArticle
    });
  };

  getRandom = number => {
    return Math.floor(Math.random() * number);
  };

  render() {
    const article = data[this.state.relationArticle];
    return (
      <Div>
        <Content>
          <h2 className="more-title">更多相關的文章</h2>
          <More>
            <p>{article.subtitle}</p>
            <div>
              <img />
            </div>
            <h3>{article.title}</h3>
            <Author>123</Author>
          </More>
        </Content>
      </Div>
    );
  }
}

export default MoreRelation;
